<template>
  <div id="Message">
    <article>
      <h2>ÁïôË®ÄÊùø</h2>
      <p>ËØ∑ÊñáÊòéÂèëË®ÄÔºåÁ¶ÅÊ≠¢ÂπøÂëä„ÄÇ‰∏çÁÑ∂ÊãâÈªë‰∫ÜÂó∑\(^o^)/~</p>
      <el-input
        type="textarea"
        :rows="2"
        placeholder="ËØ∑ËæìÂÖ•ÂÜÖÂÆπ"
        maxlength="100"
        show-word-limit
        v-model="msg">
      </el-input>
      <div class="btn">
        <i class="iconfont icon-biaoqing bq" @click.stop="showEmoji"></i>
        <el-button type="primary" size="mini" @click="handleMessageSubmit">ÂèëË°®</el-button>
        <div
          class="emoji"
          :class="{
            show: ifEmojiShow
          }"
        >
          <ul>
            <li
              v-for="(item,index) in emoji"
              :key="index"
              @click="handleEmojiClick(item)"
            >
              {{ item }}
            </li>
          </ul>
        </div>

      </div>
    </article>

    <article class="content">
      <ul>
        <li v-for="(item,index) in msgList" :key="item._id">
          <!--Áà∂ÁïôË®Ä-->
          <div class="parent">
            <!--ÁïôË®Ä-->
            <div class="left">
              <!--Áî®Êà∑Â§¥ÂÉè-->
              <img :src="baseURL+item.user.photo" alt="">
            </div>
            <div class="right">
              <div class="name-time">
                <!--Áî®Êà∑Âêç-->
                <span>{{ item.user.user }}</span>
                <!--ÁïôË®ÄÊó∂Èó¥-->
                <span>{{ formatDate(item.date) }}</span>
              </div>
              <div class="message">
                <!--ÁïôË®ÄÂÜÖÂÆπ-->
                {{ item.msg }}
              </div>
              <div class="like-reply">
                <i
                  class="like iconfont icon-xinaixin"
                  :class="{
                    isLike: userInfo._id && (item.likes.indexOf(userInfo._id) !== -1)
                  }"
                  @click="handleLikesClick(item)"
                ></i>
                <!--ÁÇπËµûÊï∞-->
                <span>{{ item.likes.length }}</span>
                <i
                  class="reply iconfont icon-huifu"

                  @click="handleReplyClick(item,item.user)"
                ></i>
              </div>
            </div>
          </div>
          <!--Â≠êÁïôË®Ä-->
          <div class="child" v-for="(childItem,index) in item.children" :key="index">
            <div class="left">
              <img :src="baseURL+childItem.user.photo" alt="">
            </div>
            <div class="right">
              <div class="name-time">
                <span>{{ childItem.user.user }}</span>
                <span>{{ formatDate(childItem.date) }}</span>
              </div>
              <div class="message">
                <span>@{{ childItem.replyUser.user }}</span>
                {{ childItem.msg }}
              </div>
              <div class="like-reply">
                <i
                  class="like iconfont icon-xinaixin"
                  :class="{
                    isLike: userInfo._id && (childItem.likes.indexOf(userInfo._id) !== -1)
                  }"
                  @click="handleLikesClick(item,childItem)"
                ></i>
                <span>{{ childItem.likes.length }}</span>
                <i
                  class="reply iconfont icon-huifu"
                  @click="handleReplyClick(item, childItem.user)"
                ></i>
              </div>
            </div>
          </div>
          <!--ÂõûÂ§çÊ°Ü-->
          <div
            class="replyInput"
            :class="{
              'show': item.ifShowReply
            }"
          >
            <el-input
              :placeholder="'@'+item.replyUser.user"
              v-model="item.reply"
              size="mini"
            ></el-input>
            <el-button
              class="replyBtn"
              type="primary"
              size="mini"
              @click="handleReplySubmit(item)"
            >ÂõûÂ§ç
            </el-button>
            <div class="clear"></div>
          </div>
        </li>
      </ul>
    </article>
  </div>
</template>

<script>
import {mapState} from "vuex"

export default {
  name: "Message",
  data() {
    return {
      msg: "",
      emoji: [
        "üòÉ", "üòÅ", "üòÇ", "üòä", "üôÉ", "ü•∞", "üòò", "ü§≠", "ü§î", "üòè",
        "üòµ", "üòü", "üôÅ", "üò≥", "üò∞", "üò≠", "üòñ", "üò°", "üò†", "üòà",
        "ü§°", "‚ò†Ô∏è", "üëª", "üôà", "üôâ", "‚ù§Ô∏è", "üíñ", "üí¢", "üí£", "üí§",
        "üëã", "üëç", "üëå", "ü§û", "ü§ü", "ü§ô", "üëä", "üñï", "ü§ù", "üôè",
        "üôÖ", "üôÖ‚Äç‚ôÇÔ∏è", "üë®‚Äç‚úàÔ∏è", "üë©‚Äç‚úàÔ∏è", "üë∑", "üë∑‚Äç‚ôÄÔ∏è", "ü§¶", "ü§¶‚Äç‚ôÇÔ∏è", "üë¥", "üßì",
        "üê¥", "üêÆ", "üêñ", "üêë", "üêì", "ü¶ö", "üê≤", "üê≥", "üêü", "üêå",
      ],
      ifEmojiShow: false,
      msgList: []
    }
  },
  computed: {
    ...mapState(['userInfo'])
  },

  methods: {
    //ÁÇπÂáªÂõæÁâáÊòæÁ§∫emoji
    showEmoji() {
      this.ifEmojiShow = true
    },

    //Ë°®ÊÉÖË¢´ÁÇπÂáª
    handleEmojiClick(item) {
      this.msg += item
    },

    //ÁïôË®ÄÂèëË°®
    async handleMessageSubmit() {
      //È™åËØÅÊòØÂê¶ÁôªÈôÜ
      if (!this.userInfo.user) {
        return this.$message.error("ËØ∑ÂÖàÁôªÂΩïÔºÅ")
      }

      //È™åËØÅÊúâÊ≤°ÊúâÂÜÖÂÆπ
      let msg = this.msg.trim()
      if (!msg) {
        return this.$message.error("ËØ∑ËæìÂÖ•ÁïôË®ÄÂÜÖÂÆπ„ÄÇ")
      }

      //ÂæÄÂêéÁ´ØÂèëËØ∑Ê±Ç
      let {data} = await this.$axios({
        method: "POST",
        url: "/msg/report",
        data: {msg}
      })

      //ÂêéÁª≠Â§ÑÁêÜ
      if (data.code) {
        return this.$message.error(data.msg)
      }

      this.$message.success("ÁïôË®ÄÊàêÂäüÔºÅ")
      this.msg = ""
      this.getMsg()
    },

    //ËØ∑Ê±ÇÁïôË®ÄÊï∞ÊçÆ
    async getMsg() {
      let {data} = await this.$axios({
        method: "GET",
        url: "/msg/get"
      })

      if (data.code) {
        return this.$message.error("ËØ∑Ê±ÇÁïôË®ÄÊï∞ÊçÆÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï")
      }

      //Ê∑ªÂä†‰∏Ä‰∫õÊñ∞Â±ûÊÄß
      data.data.filter(item => {
        //Áî®Êù•ÊéßÂà∂ÂõûÂ§çÊ°ÜÊòæ‰∏çÊòæÁ§∫
        item.ifShowReply = false
        //Áî®Êù•Â≠òÂÇ®ÊØè‰∏™ÁïôË®ÄÁöÑÂõûÂ§ç
        item.reply = ""
        //Áî®Êù•Â≠òÂÇ®ÂõûÂ§çÁöÑÊòØË∞Å
        item.replyUser = {}
      })

      //ËØ∑Ê±ÇÊàêÂäü
      this.msgList = data.data
    },

    //Â§ÑÁêÜÊó∂Èó¥Ê†ºÂºè
    formatDate(val) {
      let date = new Date(val),
        YY = date.getFullYear(),
        MM = date.getMonth() + 1,
        DD = date.getDate(),
        hh = date.getHours(),
        mm = date.getMinutes(),
        ss = date.getSeconds()

      hh < 10 && (hh = '0' + hh)
      mm < 10 && (mm = '0' + mm)
      ss < 10 && (ss = '0' + ss)

      return `${YY}-${MM}-${DD} ${hh}:${mm}:${ss}`
    },

    //ÂõûÂ§çÊåâÈíÆË¢´ÁÇπÂáª
    handleReplyClick(item, replyUser) {
      //Êú™ÁôªÂΩïÊó∂Ôºå‰∏çËÆ©ÊâìÂºÄ
      if (!this.userInfo.user) {
        return this.$message.error("ËØ∑ÂÖàÁôªÂΩï")
      }

      item.ifShowReply = !item.ifShowReply
      item.replyUser = replyUser
    },

    //ÂõûÂ§çÊèê‰∫§
    async handleReplySubmit(item) {
      let reply = item.reply.trim()

      if (!reply) {
        return this.$message.error("ËØ∑ËæìÂÖ•ÂõûÂ§çÂÜÖÂÆπ")
      }

      let {data} = await this.$axios({
        method: "POST",
        url: "/msg/reply",
        data: {
          msg: reply,
          _id: item._id,
          replyUserId: item.replyUser._id
        }
      })

      if (data.code) {
        return this.$message.error(data.msg)
      }

      //ÂõûÂ§çÊàêÂäü
      this.$message.success(data.msg)
      //ÈáçÊñ∞ËØ∑Ê±ÇÁïôË®ÄÊï∞ÊçÆ
      this.getMsg()

    },

    //ÁÇπËµû
    async handleLikesClick(item, childItem) {

      let {data} = await this.$axios({
        method: "POST"
        , url: "/msg/like"
        , data: {
          item,
          childItem
        }
      })

      if(data.code){
        return this.$message.error(data.msg)
      }

      this.getMsg()

    }
  },

  created() {
    //ËØ∑Ê±ÇÁïôË®ÄÊï∞ÊçÆ
    this.getMsg()
  },

  mounted() {
    //‰∏∫‰∫ÜËÆ©emojiÊ°ÜËÉΩÂ§üÂÖ≥Êéâ
    document.addEventListener("click", () => {
      this.ifEmojiShow = false
    })
  }
}
</script>

<style lang="less" scoped>
#Message {
  flex: 1;

  article {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
    box-shadow: 0 0 4px #ddd;
    padding: 25px 20px;
    background-color: #fff;
    font-family: Quicksand;

    h2 {
      margin-bottom: 15px;
      font-size: 20px;
      letter-spacing: 3px;
      line-height: 24px;
      border-left: 5px solid #73b899;
      text-indent: 10px;
    }

    p {
      text-indent: 2em;
      color: #444;
      font-size: 14px;
      letter-spacing: 4px;
      line-height: 30px;

      &.border {
        border-bottom: 1px dotted rgb(170, 170, 170);
      }
    }

    .el-textarea {
      min-height: 80px;
      margin-top: 20px;
    }

    /deep/ textarea {
      min-height: 80px !important;
      font-family: "Quicksand", "Microsoft YaHei", sans-serif;
      color: #444;
      font-size: 14px;
      letter-spacing: 4px;
      line-height: 30px;
    }

    .btn {
      position: relative;
      display: flex;
      margin-top: 10px;
      justify-content: space-between;

      i.bq {
        font-size: 20px;
        cursor: pointer;
      }

      .emoji {
        display: none;
        position: absolute;
        top: 30px;
        left: 0;
        width: 300px;
        height: 180px;
        padding: 10px;
        background-color: #fff;
        box-shadow: 0 0 5px #aaa;
        border-radius: 3px;
        font-size: 18px;

        &.show {
          display: block;
        }

        &::before {
          content: "";
          position: absolute;
          top: -10px;
          left: 5px;
          width: 0;
          height: 0;
          border: 5px solid transparent;
          border-bottom-color: #bbb;
        }

        ul {
          display: flex;
          flex-wrap: wrap;
          width: 100%;
          height: 100%;
          border-top: 1px solid #ddd;
          border-left: 1px solid #ddd;

          li {
            box-sizing: border-box;
            width: 30px;
            height: 30px;
            line-height: 29px;
            text-align: center;
            border-right: 1px solid #ddd;
            border-bottom: 1px solid #ddd;
            cursor: pointer;
          }
        }

      }
    }


    &.content {
      user-select: none;

      ul {
        li {
          padding-bottom: 5px;
          margin-bottom: 10px;
          border-bottom: 1px dashed #ddd;
          font-size: 12px;
          font-family: "Microsoft YaHei", "sans-serif";

          div.parent, div.child {
            display: flex;
          }

          div.child {
            margin-left: 55px;
            margin-top: 3px;
          }

          div.left {
            overflow: hidden;
            width: 40px;
            height: 40px;
            margin-right: 15px;
            border-radius: 50%;

            img {
              width: 100%;
              height: 100%;
            }
          }

          div.right {
            flex: 1;

            div.name-time {
              font-size: 12px;
              margin-bottom: 3px;

              span:nth-child(1) {
                color: #73b899;
              }

              span:nth-child(2) {
                color: #aaa;
                margin-left: 10px;
                font-family: "Microsoft YaHei", "sans-serif";
              }
            }

            div.message {
              color: #666;
              letter-spacing: 2px;
              margin-bottom: 3px;
              word-break: break-word;

              span {
                color: #409eff;
                margin-right: 5px;
              }
            }

            div.like-reply {
              i {
                cursor: pointer;
                font-size: 14px;
                color: #aaa;
              }

              i.isLike {
                color: #f30606;
              }

              span {
                display: inline-block;
                width: 43px;
                font-size: 12px;
                color: #aaa;
              }
            }
          }
        }
      }
    }

    .replyInput {
      box-sizing: border-box;
      overflow: hidden;
      width: 100%;
      height: 0;
      padding-left: 55px;
      transition: height .3s;

      &.show {
        height: 57px;
      }

      .replyBtn {
        padding: 4px 9px !important;
        float: right;
      }
    }

  }
}
</style>














